.card-wrapper {    
    width: 20rem;
    height: 23rem;
    margin: 2rem;
    color: var(--white);
    position: relative;
    border-radius: 5px;
    box-shadow: -1px -1px 5px rgba(200, 165, 106, 0.555), 1px 1px 5px rgba(200, 165, 106, 0.555);
}

.img-wrapper{
    width: 100%;
    height: auto;
    position: absolute;
    z-index: -1;
}

.img-wrapper img {
    width: 100%;
    
}

.description-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    height: 90%;
    width: 100%;
    overflow: hidden;
    transition: .3s ease;
    background-color: rgba(47, 48, 51, 0.877);
    border-radius: 5px;
}

.card-wrapper:hover .description-wrapper {
    opacity: 1;
}

.description2 {
    padding: 0 1rem 3rem;
}

.project-links2 {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.github {
    width: 2rem;
    height: auto;
}

.card-anchor {
    text-decoration: none;
}

.card-anchor:link,    
.card-anchor:visited {
    color: black;
}

button {
    padding: 0.4rem;
    border: none;
    border-radius: 2px;
}

button:hover{
    background-color: var(--gold);
}

.project-links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}